    font-size: 2.625rem;
  }
}

.schedule {
  width: 100%;
}
.schedule--sec {
  width: 14.625rem;
  margin: 0 auto;
}
.schedule__title {
  font-family: "Montserrat Semi", Verdana, Arial, sans-serif;
  font-size: 1.125rem;
  color: #ffffff;
  text-align: center;
  margin: 0 0 1rem;
  display: none;
}
.schedule--sec .schedule__title {
  display: block;
  color: #202454;
}
@media (max-width: 639px) {
  .schedule__title {
    display: block;
  }
}
.schedule__body-wrap {
  display: flex;
  align-self: flex-start;
  gap: 10px;
}
.schedule__icon {
  width: 2rem;
  height: 2rem;
  flex-shrink: 0;
}
.schedule__icon img {
  width: 100%;
  height: 100%;
}
.schedule__body {
  width: 100%;
  padding: 0.5625rem 0 0;
}
.schedule__item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1;
  color: #ffffff;
}
.schedule--sec .schedule__item {
  display: grid;
  justify-content: flex-start;
  grid-template-columns: 3.75rem 1fr;
  color: #202454;
}
.schedule__item + .schedule__item {
  margin: 1.0625rem 0 0;
}
.schedule__bottom {
  font-size: 0.75rem;
  font-weight: 400;
  line-height: 1;
  color: #ffffff;
  text-align: center;
  margin: 0.25rem 0 0;
}
