  .seo-block p {
    font-size: 1rem;
  }
}
.seo-block button {
  display: inline-block;
  font-size: 0.75rem;
  color: #4abbf3;
  line-height: 1;
  border-bottom: 1px solid #4abbf3;
  transform: translateY(-1px);
  transition: color 0.2s ease, border-color 0.2s ease;
}
