  border-color: #202454;
}

.brands-block__title {
  display: block;
  font-size: 1.125rem;
  font-weight: 500;
  color: #202454;
  text-align: center;
  margin: 0 0 0.75rem;
}
@media (min-width: 745px) {
  .brands-block__title {
    font-size: 2.25rem;
  }
}
.brands-block__title:hover {
