}
.brands-block__slider {
  overflow: hidden;
  position: relative;
  padding: 0 0 2.375rem;
}
.brands-block__slider .swiper-pagination {
  bottom: 0;
}
.brands-block__slider .swiper-btn {
  position: absolute;
  top: 38%;
  z-index: 2;
  transform: translateY(-50%);
}
.brands-block__slider .swiper-btn.js-next-swiper {
  right: 0;
}
.brands-block__slider .swiper-btn.js-prev-swiper {
  left: 0;
}
@media (max-width: 991px) {
  .brands-block__slider .swiper-btn {
