  }
}
.brands-block__img {
  display: flex;
  justify-content: center;
  max-width: 7.5rem;
  margin: 0 auto;
}
.brands-block__img img {
  width: 100%;
  height: auto;
  object-fit: contain;
  transition: transform 0.2s ease;
}
.brands-block__img:hover img {
  transform: scale(1.05);
}

.blue-dealer {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  padding: 0.9375rem 0;
}
@media (min-width: 700px) {
  .blue-dealer {
    font-size: 2.25rem;
    padding: 1.4375rem 0;
  }
}
@media (min-width: 1200px) {
  .blue-dealer {
    font-size: 2.625rem;
    padding: 1.1875rem 0;
  }
}

.top-section {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 1.25rem;
  position: relative;
}
.top-section .more-link:hover {
  color: #202454;
}
.top-section .more-link:hover .more-link__text {
  color: #202454;
}
.top-section .more-link:hover .more-link__text::after {
  transform: scaleX(1);
}
.top-section .more-link__text {
  position: relative;
}
.top-section .more-link__text::after {
  content: "";
  position: absolute;
  top: calc(100% + 0.125rem);
  left: 0;
  width: 100%;
  height: 0.0625rem;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.2s ease;
  background-color: #202454;
}
.top-section--green-back {
  padding: 0.6875rem 0;
}
@media (min-width: 745px) {
  .top-section--green-back {
    padding: 1.4375rem 0;
  }
}
.top-section--green-back > * {
  position: relative;
  z-index: 2;
}
.top-section--green-back::after {
  content: "";
  display: block;
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  background-color: #c6da83;
}
@media (max-width: 744px) {
  .top-section--green-back {
    align-items: center;
  }
  .top-section--green-back .more-link__text {
    display: none;
  }
}

