}
.mark-block--2 svg:nth-child(-n+2) {
  fill: #fe9382;
}
.mark-block--3 svg:nth-child(-n+3) {
  fill: #fe9382;
}
.mark-block--4 svg:nth-child(-n+4) {
  fill: #fe9382;
}
.mark-block--5 svg:nth-child(-n+5) {
  fill: #fe9382;
}

