  width: 2px;
  background-color: #dbebfc;
}

.categ-item {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 0.625rem;
  background-color: #dbebfc;
  border-radius: 0.875rem;
  padding: 0.6875rem;
  transition: background-color 0.2s ease;
}
.categ-item__icon {
  width: 2rem;
  height: 2rem;
  flex-shrink: 0;
}
.categ-item__icon img {
  width: 100%;
  height: 100%;
}
.categ-item__text {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2;
  color: #202454;
  text-align: left;
}
.categ-item--mob .categ-item__text {
  font-size: 1.125rem;
  margin: 0 0.3125rem 0 0;
}
.categ-item__arr {
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 0 0 auto;
  flex-shrink: 0;
  transition: transform 0.2s ease;
}
.categ-item__arr img {
  width: 100%;
  height: 100%;
}
@media (min-width: 1280px) {
  .categ-item:hover {
    background-color: #b5d8fd;
  }
  .categ-item + .categ-list,
  .categ-item + .categ-item {
    margin: 0.625rem 0 0;
  }
}
.categ-item--mob + .categ-item--mob {
  margin: 0.5rem 0 0;
}
.active .categ-item .categ-item__arr {
  transform: scaleY(-1);
}

.categ-list {
  display: flex;
  flex-direction: column;
  padding: 0.75rem 1.8125rem;
  gap: 0.8125rem;
  background-color: #f0f6fd;
  border-radius: 0.875rem;
}
.categ-list a {
  display: block;
  font-size: 0.875rem;
  color: #202454;
}
.categ-list a:hover {
  color: #4abbf3;
}
@media (min-width: 1280px) {
  .categ-list + .categ-item {
    margin: 0.75rem 0 0;
  }
}
.categ-list--mob {
  background-color: transparent;
  padding: 0;
  gap: 0.5625rem;
}
.categ-list--mob.js-acc-block {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out, margin 0.3s ease-in-out;
}
.active .categ-list--mob {
  margin: 0.5rem 0;
}
.categ-list--mob a {
  border-radius: 0.8125rem;
  background-color: #f0f6fd;
  padding: 0.75rem 0.625rem 0.75rem 1.875rem;
}

.menu-categories__body {
  display: flex;
  flex-direction: column;
  gap: 0.4375rem;
  margin: 0.6875rem 0 0;
}
.menu-categories__body.js-acc-block-m {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out, margin 0.3s ease-in-out;
}
.active-m .menu-categories__body {
  margin: 0.6875rem 0;
}
.menu-categories:last-child .m-category__img {
  width: 9.375rem;
  margin: 0 0 0 auto;
}

.mobile-menu {
  display: flex;
  justify-content: flex-end;
  position: fixed;
  width: 100%;
  left: 0;
  top: 7.5rem;
  height: calc(100% - 120px);
  z-index: 10;
  background-color: rgba(64, 64, 64, 0.8);
  backdrop-filter: blur(3.125rem);
  justify-content: flex-end;
  opacity: 0;
  visibility: hidden;
  transform: translateY(5%);
  transition: transform 0.2s cubic-bezier(0, 0, 0.2, 1), opacity 0.2s ease, visibility 0.2s ease;
}
.mobile-menu.show {
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}
@media (max-width: 991px) {
  .mobile-menu {
    top: 5rem;
    height: calc(100% - 80px);
  }
}
@media (max-width: 744px) {
  .mobile-menu {
    top: 3.125rem;
    height: calc(100% - 50px);
  }
}
.mobile-menu__top {
  padding: 1.0625rem 0;
}
.mobile-menu__top .lang-block {
  justify-content: flex-end;
}
.mobile-menu__top .lang-block__link {
  color: #737488;
}
.mobile-menu__body {
  width: 100%;
  height: 100%;
  max-width: 25.5rem;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  overflow: auto;
  overflow-x: hidden;
  padding: 0 1rem 3.75rem;
}
@media (min-width: 745px) {
  .mobile-menu__body {
    padding: 0 2.5rem 3.75rem;
  }
}
.mobile-menu__links {
  margin: 1.0625rem 0 0;
}
.mobile-menu__pc {
  margin: 2.25rem 0 0;
  padding: 0 1.25rem;
}
.mobile-menu__pc .mpc-link + .mpc-link {
  margin: 0.6875rem 0 0;
}
.mobile-menu__pc-title {
  font-family: "Montserrat Semi", Verdana, Arial, sans-serif;
  font-size: 0.875rem;
  color: #202454;
  margin: 0 0 0.875rem;
}
.mobile-menu__dop-links {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 1.75rem 0 0;
}
.mobile-menu__dop-links a {
  display: flex;
  justify-content: center;
  position: relative;
  font-size: 1rem;
  font-weight: 400;
  color: #202454;
  border-bottom: 1px solid #dbebfc;
  padding: 0.75rem 0;
  min-width: 11.25rem;
}
.mobile-menu__dop-links a:hover {
  color: #4abbf3;
}
.mobile-menu .schedule {
  margin: 2.0625rem auto 0;
}
.mobile-menu .schedule__body-wrap {
  transform: translateX(2px);
}
.mobile-menu__soc-title {
  font-family: "Montserrat Semi", Verdana, Arial, sans-serif;
  font-size: 1.125rem;
  color: #202454;
  text-align: center;
  margin: 0 0 0.875rem;
}
.mobile-menu__soc {
  margin: 1.125rem 0 0;
}
.mobile-menu__soc-list {
  display: flex;
  justify-content: center;
  gap: 0.75rem;
}

.emp-link {
  display: flex;
  align-items: center;
  gap: 0.3125rem;
}
.emp-link__icon {
  width: 1.25rem;
  height: 1.25rem;
}
.emp-link__icon img {
  width: 100%;
  height: 100%;
}
.emp-link__text {
  font-size: 0.875rem;
  font-weight: 400;
  text-transform: lowercase;
  line-height: 1;
  color: #4abbf3;
  position: relative;
}
.emp-link__text::after {
  content: "";
  position: absolute;
  top: calc(100% + 0.0625rem);
  left: 0;
  width: 100%;
  height: 0.0625rem;
  z-index: 2;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.2s ease;
  background-color: #202454;
}
.emp-link__text::before {
  content: "";
  position: absolute;
  top: calc(100% + 0.0625rem);
  left: 0;
  width: 100%;
  height: 0.0625rem;
  z-index: 1;
  transform: scaleX(1);
  transform-origin: left;
  transition: transform 0.2s ease;
  background-color: #4abbf3;
}
.emp-link:hover .emp-link__text {
  color: #202454;
}
.emp-link:hover .emp-link__text::after {
  transform: scaleX(1);
}

.mpc-link {
  display: flex;
  align-items: center;
  gap: 10px;
}
.mpc-link__icon {
  width: 1.25rem;
  height: 1.25rem;
}
.mpc-link__icon img {
  width: 100%;
  height: 100%;
}
.mpc-link__text {
  font-size: 0.875rem;
  line-height: 1;
  color: #202454;
  position: relative;
}
.mpc-link__text::after {
  content: "";
  position: absolute;
  top: calc(100% + -0.0625rem);
  left: 0;
  width: 100%;
  height: 0.0625rem;
  z-index: 2;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.2s ease;
  background-color: #4abbf3;
}
.mpc-link:hover {
  color: #4abbf3;
}
.mpc-link:hover .mpc-link__text::after {
  transform: scaleX(1);
}

.m-category {
  display: flex;
  justify-content: flex-end;
  border-radius: 1rem;
  overflow: hidden;
  background-color: #b5d8fd;
  width: 100%;
  height: 4.75rem;
  position: relative;
}
.m-category__img {
  width: 7.5rem;
  height: 4.75rem;
  margin: 0 1.9375rem 0 0;
  background-image: url("data:image/webp;base64,");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  position: relative;
}
.m-category__img::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  max-width: 18.75rem;
  top: 50%;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, -50%);
  border-radius: 100%;
  background-color: rgba(255, 255, 255, 0.6);
  filter: blur(60px);
}
.m-category__img img {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 2;
}
.m-category__title {
  position: absolute;
  top: 50%;
  left: 1.25rem;
  z-index: 1;
  transform: translateY(-50%);
  font-family: "Montserrat Semi", Verdana, Arial, sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  color: #202454;
}
@media (max-width: 360px) {
  .m-category__title {
    font-size: 6.3vw;
  }
}
.m-category__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: 50%;
  right: 0.6875rem;
  z-index: 1;
  transform: translateY(-50%);
  transform-origin: top;
  transition: transform 0.2s ease;
}
.m-category__icon img {
  width: 100%;
  height: 100%;
}
.active-m .m-category .m-category__icon {
  transform: scaleY(-1) translateY(-50%);
}

.banner-block {
  display: block;
  width: 100%;
  height: auto;
  min-height: 6.25rem;
  position: relative;
}
.banner-block__img-link {
  display: block;
  width: 100%;
}
.banner-block .mid-btn {
  width: 100%;
  max-width: 20rem;
  position: absolute;
  z-index: 1;
}
@media (max-width: 430px) {
  .banner-block .mid-btn {
    position: relative;
    max-width: 100%;
    margin: 0.625rem 0 0;
  }
}
@media (min-width: 431px) {
  .banner-block--right .mid-btn {
    top: 52%;
    left: 70%;
    transform: translate(-50%, -50%);
  }
}
@media (min-width: 431px) and (max-width: 744px) {
  .banner-block--right .mid-btn {
    left: 50%;
  }
}
.banner-block__img {
  display: block;
  width: 100%;
  height: 100%;
}
.banner-block__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-count {
  display: flex;
  align-items: center;
  width: 5rem;
  height: 1.75rem;
  border: 1px solid #dbebfc;
  border-radius: 0.5625rem;
  padding: 0 0.5625rem;
}
.product-count__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 0.75rem;
  height: 0.75rem;
  flex-shrink: 0;
  color: #202454;
  transition: color 0.2s ease;
}
.product-count__button svg {
  width: 100%;
  height: 100%;
  transition: fill 0.2s ease;
}
.product-count__button:hover svg, .product-count__button:focus svg {
  fill: #4abbf3;
}
.product-count__input {
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 1rem;
  color: #202454;
}

.reg-mob {
  margin: 2rem 0 0;
}
.reg-mob__btns {
  display: flex;
  justify-content: space-between;
  margin: 0.5rem 0 0;
}
.reg-mob__btns .mid-btn {
  font-size: 0.875rem;
  min-width: initial;
  width: 48%;
  height: 2.625rem;
}

.reg-board {
  width: 100%;
  border-radius: 0.75rem;
  background-color: #dbebfc;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 1.5rem 0.875rem;
}
.reg-board__title {
  font-size: 1rem;
  font-weight: bold;
  color: #202454;
  margin: 0 0 0.5rem;
}
.reg-board__sub {
  font-size: 0.875rem;
  color: #202454;
  margin: 0 0 0.4375rem;
}
.reg-board__sale {
  display: flex;
  align-items: center;
  gap: 5px;
}
.reg-board__sale span {
  line-height: 1;
  font-size: 1.5rem;
  font-weight: 900;
  color: #202454;
}