@import "abstracts/functions";
@import "abstracts/vars";

.container {
	width: 100%;
	max-width: rem(1360);
	margin: 0 auto;
	padding: 0 rem(16);

	@media(min-width: 745px) {
		padding: 0 rem(40);
	}
}

.section {

	&--banner {
		margin: rem(20) 0 0;

		@media(min-width: 992px) {
			margin: rem(10) 0 0;
		}
	}

	&--reg {
		padding: rem(30) 0;

		@media(min-width: 992px) {
			padding: rem(60) 0;
		}
	}

	&--top-categories {
		position: relative;

		@media(min-width: 992px) {
			padding: rem(5) 0;
		}
	}

	&--care {
		background-color: #d6e6fd;
	}

	&--mid-categories {
		padding: rem(40) 0;

		@media(min-width: 1200px) {
			padding: rem(30) 0 rem(40);
		}
	}

	&--home-advantages {
		padding: rem(30) 0 rem(20);
	}

	&--home-slider {
		overflow: hidden;
		padding: rem(32) 0 0;
	}

	&--sales-slider {
		padding: rem(51) 0;
		overflow: hidden;
	}

	&--news-slider {
		padding: 0 0 rem(20);
		overflow: hidden;

		@media(min-width: 745px) {
			padding: 0 0 rem(51);
		}
	}

	&--reviews-slider {
		padding: rem(45) 0;
		overflow: hidden;

		@media(min-width: 745px) {
			padding: rem(51) 0;
		}
	}

	&--boards {
		padding: 0 0 rem(36);
		overflow: hidden;
	}

	&--blue-dealer {
		background-color: $colorBlueDark;
	}

	&--brands-block {
		padding: rem(28) 0;
	}

	&--seo-block {
		padding: rem(40) 0 rem(9);
		display: block;

		@media(min-width: 745px) {
			padding: rem(20) 0 rem(9);
		}
	}
}

.title {

	&--section {
		font-size: rem(24);
		line-height: 1;
		font-weight: bold;
		color: $colorBlueDark;

		@media(min-width: 745px) {
			font-size: rem(36);
		}

		@media(min-width: 992px) {
			font-size: rem(42);
		}
	}
}

.schedule {
	width: 100%;

	&--sec {
		width: rem(234);
		margin: 0 auto;
	}

	&__title {
		font-family: $secFont;
		font-size: rem(18);
		color: #ffffff;
		text-align: center;
		margin: 0 0 rem(16);
		display: none;

		.schedule--sec & {
			display: block;
			color: $colorBlueDark;
		}

		@media(max-width: 639px) {
			display: block;
		}
	}

	&__body-wrap {
		display: flex;
		align-self: flex-start;
		gap: 10px;
	}

	&__icon {
		width: rem(32);
		height: rem(32);
		flex-shrink: 0;

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__body {
		width: 100%;
		padding: rem(9) 0 0;
	}

	&__item {
		width: 100%;
		display: flex;
		justify-content: space-between;
		font-size: rem(16);
		font-weight: 400;
		line-height: 1;
		color: #ffffff;

		.schedule--sec & {
			display: grid;
			justify-content: flex-start;
			grid-template-columns: rem(60) 1fr;
			color: $colorBlueDark;
		}

		+ .schedule__item {
			margin: rem(17) 0 0;
		}
	}

	&__bottom {
		font-size: rem(12);
		font-weight: 400;
		line-height: 1;
		color: #ffffff;
		text-align: center;
		margin: rem(4) 0 0;

		.schedule--sec & {
			color: $colorBlueDark;
		}
	}
}

.mail-link {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: rem(10);
	margin: rem(40) 0 0;

	@media(min-width: 992px) {
		margin: rem(24) 0 0;
	}

	&__icon {
		width: rem(32);
		height: rem(32);
		color: $colorBlueLight;
	}

	&__text {
		font-size: rem(14);
		color: $colorBlueDark;
		transition: color 0.2s ease;
	}

	&:hover {

		.mail-link__text {
			color: $colorBlueLight;
		}
	}
}

.seo-block {
	text-align: center;

	h2 {
		font-size: rem(24);
		font-weight: bold;
		color: $colorBlueDark;
		text-align: center;
		margin: 0 0 rem(14);

		@media(min-width: 745px) {
			font-size: rem(36);
			margin: 0 0 rem(26);
		}

		@media(min-width: 1200px) {
			font-size: rem(42);
		}
	}

	p {
		display: block;
		font-size: rem(12);
		line-height: 1.2;
		font-weight: 400;
		color: $colorBlueDark;
		text-align: center;
		margin: 0 0 rem(22);

		@media(min-width: 860px) {
			font-size: rem(16);
		}
	}

	button {
		display: inline-block;
		font-size: rem(12);
		color: #4abbf3;
		line-height: 1;
		border-bottom: 1px solid #4abbf3;
		transform: translateY(-1px);
		transition: color 0.2s ease, border-color 0.2s ease;

		@media(min-width: 860px) {
			font-size: rem(16);
		}

		&:hover {
			color: $colorBlueDark;
			border-color: $colorBlueDark;
		}
	}
}

.brands-block {

	&__title {
		display: block;
		font-size: rem(18);
		font-weight: 500;
		color: $colorBlueDark;
		text-align: center;
		margin: 0 0 rem(12);

		@media(min-width: 745px) {
			font-size: rem(36);
		}

		&:hover {
			color: $colorBlueLight;
		}
	}

	&__slider {
		overflow: hidden;
		position: relative;
		padding: 0 0 rem(38);

		.swiper-pagination {
			bottom: 0;
		}

		.swiper-btn {
			position: absolute;
			top: 38%;
			z-index: 2;
			transform: translateY(-50%);

			&.js-next-swiper {
				right: 0;
			}

			&.js-prev-swiper {
				left: 0;
			}

			@media(max-width: 991px) {
				display: none;
			}
		}
	}

	&__img {
		display: flex;
		justify-content: center;
		max-width: rem(120);
		margin: 0 auto;

		img {
			width: 100%;
			height: auto;
			object-fit: contain;
			transition: transform 0.2s ease;
		}

		&:hover {

			img {
				transform: scale(1.05);
			}
		}
	}
}

.blue-dealer {
	text-align: center;
	font-size: rem(24);
	font-weight: bold;
	text-transform: uppercase;
	color: #ffffff;
	padding: rem(15) 0;

	@media(min-width: 700px) {
		font-size: rem(36);
		padding: rem(23) 0;
	}

	@media(min-width: 1200px) {
		font-size: rem(42);
		padding: rem(19) 0;
	}
}

.top-section {
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	gap: rem(20);
	position: relative;

	.more-link {

		&:hover {
			color: $colorBlueDark;

			.more-link__text {
				color: $colorBlueDark;

				&::after {
					transform: scaleX(1);
				}
			}
		}

		&__text {
			position: relative;

			&::after {
				content: '';
				position: absolute;
				top: calc(100% + #{rem(2)});
				left: 0;
				width: 100%;
				height: rem(1);
				z-index: 1;
				transform: scaleX(0);
				transform-origin: left;
				transition: transform 0.2s ease;
				background-color: $colorBlueDark;
			}
		}
	}

	&--green-back {
		padding: rem(11) 0;

		@media(min-width: 745px) {
			padding: rem(23) 0;
		}

		> * {
			position: relative;
			z-index: 2;
		}

		&::after {
			content: '';
			display: block;
			width: 100vw;
			height: 100%;
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateX(-50%);
			z-index: 1;
			background-color: #c6da83;
		}

		@media(max-width: 744px) {
			align-items: center;

		    .more-link__text {
			    display: none;
		    }
		}
	}
}

.fire-board {
	display: flex;
	align-items: center;
	gap: rem(5);
	background-color: #dbebfc;
	border-radius: rem(6);
	padding: rem(4) rem(8);

	@media(min-width: 1200px) {
		gap: rem(7);
		padding: rem(10) rem(11);
	}

	&--red {
		background-color: #fcd8da;
	}

	&--mini {
		gap: rem(4);
		padding: rem(4) rem(6);

		@media(min-width: 1200px) {
			gap: rem(4);
			padding: rem(4) rem(6);
		}
	}

	&__icon {
		width: rem(16);
		height: rem(18);

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		font-family: $secFont;
		font-size: rem(12);
		line-height: 1;
		font-weight: bold;
		color: $colorBlueDark;

		@media(min-width: 745px) {
			font-family: $mainFont;
			font-size: rem(16);
		}

		.fire-board--mini & {
			margin: rem(3) 0 0;
		}

		.fire-board--mini & {
			@media(min-width: 745px) {
				font-family: $secFont;
				font-size: rem(12);
				margin: rem(2) 0 0;
			}
		}
	}
}

.mark-block {
	display: flex;
	align-items: center;

	svg {
		width: rem(10);
		height: rem(10);
		fill: none;

		@media(min-width: 745px) {
			width: rem(19);
			height: rem(19);
		}
	}

	&--small {

		svg {
			width: rem(10);
			height: rem(10);

			@media(min-width: 745px) {
				width: rem(12);
				height: rem(12);
			}
		}
	}

	@for $i from 1 through 5 {
		&--#{$i} {
			svg:nth-child(-n + #{$i}) {
				fill: #fe9382;
			}
		}
	}
}

.reviews-slider {
	width: 100%;
	margin: rem(26) 0 0;
	padding: 0 0 rem(34);
	position: relative;

	@media(min-width: 745px) {
		width: calc(100% + 40px);
		margin: rem(26) 0 0 -20px;
		padding: 0 rem(20) rem(34);
	}

	@media(min-width: 1200px) {
		overflow: hidden;
	}

	.swiper-slide {
		height: auto;

		.review-item {
			height: 100%;
		}
	}

	.swiper-pagination {
		bottom: 0;
	}

	.swiper-btn {
		position: absolute;
		top: 45%;
		z-index: 2;
		transform: translateY(-50%);

		&.js-next-swiper {
			right: rem(6);
		}

		&.js-prev-swiper {
			left: rem(6);
		}

		@media(max-width: 991px) {
			display: none;
		}
	}
}

.review-item {
	display: flex;
	flex-direction: column;
	border: 1px solid rgba(#4ABBF3, 0.3);
	border-radius: rem(24);
	position: relative;
	padding: rem(18) rem(12) rem(14);

	.mark-block {
		position: absolute;
		top: rem(17);
		left: rem(13);

		@media(min-width: 745px) {
			top: rem(22);
			left: rem(26);
		}

		svg {
			margin: 0 rem(3) 0 0;

			@media(min-width: 745px) {
				margin: 0 rem(5) 0 0;
			}
		}
	}

	&__img {
		width: rem(126);
		height: rem(134);
		margin: 0 auto;

		img {
			width: 100%;
			height: auto;
			object-fit: cover;
			transition: transform 0.2s ease;
		}

		&:hover {

			img {
				transform: scale(1.05);
			}
		}
	}

	&__back {
		width: rem(59);
		height: rem(59);
		position: absolute;
		z-index: -1;

		@media(min-width: 745px) {
			width: rem(69);
			height: rem(69);
		}

		img {
			width: 100%;
			height: 100%;
		}

		&--first {
			left: 0;
			top: 33%;
		}

		&--second {
			right: rem(10);
			bottom: 4%;
		}
	}

	&__text {
		max-width: rem(230);
		margin: rem(12) auto rem(30);
		padding: rem(19) 0 0;
		font-size: rem(14);
		line-height: 1.2;
		font-style: italic;
		color: $colorBlueDark;
		display: -webkit-box;
		-webkit-line-clamp: 6;
		-webkit-box-orient: vertical;
		overflow: hidden;
		position: relative;

		@media(min-width: 745px) {
			text-align: center;
		}

		&::after {
			content: '';
			display: block;
			width: rem(152);
			height: rem(1);
			background-color: rgba(#4ABBF3, 0.3);
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateX(-50%);
			z-index: 1;
		}
	}

	&__bottom {
		display: flex;
		flex-direction: column;
		margin: auto 0 0;
		gap: 5px;
	}

	&__name {
		font-family: $secFont;
		font-size: rem(12);
		color: $colorBlueDark;
	}

	&__date {
		font-size: rem(12);
		font-weight: 400;
		color: $colorBlueDark;
	}
}


.news-slider {
	width: 100%;
	margin: rem(25) 0 0 0;
	padding: 0 0 rem(38);
	position: relative;
	transition: width 0.4s ease;

	@media(min-width: 745px) {
		overflow: hidden;
	}

	@media(min-width: 745px) and (max-width: 1199px) {
		width: calc(100% + #{rem(100)});
	}

	@media(min-width: 1200px) {
		width: calc(100% + #{rem(40)});
		margin: rem(25) 0 0 rem(-20);
		padding: 0 rem(20) rem(38);
	}

	//&.end {
	//	@media(max-width: 1199px) {
	//		width: 100%;
	//	}
	//}

	.swiper-slide {
		@media(min-width: 745px) and (max-width: 1199px) {
		    transition: transform 0.2s ease;

			&.swiper-slide-next {
				transform: translateX(-28%);

				+ .swiper-slide {
					transform: translateX(-57%);

					.cart-item::after {
						opacity: 1;
					}
				}
			}
		}
	}

	.swiper-wrapper {
		z-index: 11;
	}

	.swiper-pagination {
		bottom: 0;
	}

	.swiper-slide {
		height: auto;

		.cart-item {
			height: 100%;
		}
	}

	.swiper-btn {
		position: absolute;
		top: 47%;
		z-index: 12;
		transform: translateY(-50%);

		&.js-next-swiper {
			right: rem(5);
		}

		&.js-prev-swiper {
			left: rem(5);
		}

		@media(max-width: 1199px) {
			display: none;
		}
	}
}

.cart-item {
	border-radius: rem(14);
	padding: rem(8) rem(10) 0;
	border: 1px solid transparent;
	transition: border-color 0.2s ease;
	position: relative;

	@media(min-width: 745px) {
		padding: rem(20) rem(10) rem(18);
	}

	.swiper-slide & {

		@media(min-width: 745px) and (max-width: 1199px) {
			width: 70%;
		}
	}

	.mark-block {
		svg {
			margin: 0 0 0 2px;

			@media(min-width: 745px) {
				margin: 0 0 0 rem(1);
			}
		}
	}

	.fire-board {
		position: absolute;
		top: rem(10);
		left: rem(6);
		z-index: 1;

		@media(min-width: 745px) {
			left: 0;
		}
	}

	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		border-radius: rem(14);
		box-shadow: 0 rem(4) rem(4) rgba(#000, 0.25);
		transition: opacity 0.2s ease;
		opacity: 0;

		@media(max-width: 744px) {
			opacity: 1;
		}
	}

	.swiper-slide-visible & {
		&::after {
			opacity: 1;
		}
	}

	&:hover {
		border-color: #dde0cc;

		&::after {
			opacity: 0;
		}
	}

	&__img {
		display: block;
		width: 100%;
		height: rem(134);

		@media(min-width: 745px) {
			height: rem(174);
		}

		@media(min-width: 1200px) {
			height: rem(230);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
			transition: transform 0.2s ease;
		}
	}

	&__text {
		display: block;
		line-height: 1.2;
		font-size: rem(14);
		color: $colorBlueDark;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
		transition: color 0.2s ease;
		margin: 0 0 rem(4);

		@media(min-width: 745px) {
			-webkit-line-clamp: 4;
			margin: 0 0 rem(20);
		}

		@media(max-width: 744px) {
		    min-height: rem(51);
		}

		&:hover {
			color: $colorBlueLight;
		}
	}

	&__mid-wrap {
		margin: auto 0 0;
	}

	&__mid {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: rem(5);
		margin: 0 0 rem(25);

		@media(min-width: 745px) {
			gap: rem(20);
		}

		@media(max-width: 744px) {
		    min-height: rem(28);
		}
	}

	&__body {
		border-top: 1px solid #dde0cc;
		padding: rem(8) 0 rem(6);
		margin: rem(12) 0 0;
		display: flex;
		flex-direction: column;
		height: calc(100% - 153px);

		@media(min-width: 745px) {
			padding: rem(20) rem(12);
			margin: rem(16) 0 0;
			height: calc(100% - 193px);
		}

		@media(min-width: 1200px) {
			height: calc(100% - 247px);
		}

		@media(min-width: 1340px) {
			padding: rem(20) rem(22);
		}
	}

	&__btns {
		display: flex;
		align-items: center;
		gap: rem(8);

		@media(min-width: 745px) {
			gap: rem(12);
		}

		@media(min-width: 1280px) {
			gap: rem(7);
		}
	}

	&__price-wrap {
		display: flex;
		flex-direction: column;
		text-align: right;

		@media(min-width: 745px) {
			gap: rem(5);
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media(min-width: 745px) {
			align-items: flex-end;
		}
	}

	&__price-old {
		font-family: $secFont;
		font-size: rem(12);
		color: $colorBlueDark;
		text-decoration: line-through;

		@media(min-width: 745px) {
			font-size: rem(14);
		}
	}

	&__price {
		font-size: rem(12);
		font-weight: bold;
		color: #59060f;

		@media(min-width: 745px) {
			font-size: rem(16);
		}
	}
}

.cart-select {
	width: rem(56);
	position: relative;

	@media(min-width: 745px) {
		width: rem(80);
	}

	&__body {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: rem(5);
		border-radius: rem(4);
		padding: 0;
		cursor: pointer;
		position: relative;
		z-index: 2;
		transition: border-color 0.2s ease;

		@media(min-width: 745px) {
			padding: 0 rem(7) 1px;
			border-bottom: rem(1) solid $colorBlueDark;
		}

		&:hover {
			border-color: $colorBlueLight;
		}
	}

	&__text {
		font-size: rem(14);
		color: $colorBlueDark;

		@media(min-width: 745px) {
			font-size: rem(16);
		}
	}

	&__drop {
		flex-direction: column;
		border: 1px solid rgba($colorBlueDark, 0.5);
		border-radius: 0 0 rem(4) rem(4);
		position: absolute;
		top: calc(100% + 2px);
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;
		width: calc(100% - 1px);
		background-color: #fff;
		max-height: rem(78);
		overflow: auto;
		display: none;

		@media(min-width: 745px) {
			border-top: none;
			top: calc(100% - 1px);
		}

		&.open {
