	width: 100vw;
	max-height: 71vh;
	top: 100%;
	left: 50%;
	transform: translateX(-50%) translate(0, 50px);
	background-color: #fff;
	z-index: 8;
	box-shadow: 0 rem(4) rem(4) rgba(#000, 0.12);
	padding: rem(12) 0;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.5s ease, visibility 0.5s ease, transform 0.5s ease;

