			border-radius: rem(13);
			background-color: #f0f6fd;
			padding: rem(12) rem(10) rem(12) rem(30);
		}
	}
}

.menu-categories {

	&__body {
		display: flex;
		flex-direction: column;
		gap: rem(7);
		margin: rem(11) 0 0;

		&.js-acc-block-m {
			max-height: 0;
			overflow: hidden;
			transition: max-height 0.3s ease-in-out, margin 0.3s ease-in-out;
		}

		.active-m & {
			margin: rem(11) 0;
		}
	}

	&:last-child {
		.m-category__img {
			width: rem(150);
			margin: 0 0 0 auto;
		}
	}
}

.mobile-menu {
	display: flex;
	justify-content: flex-end;
	position: fixed;
	width: 100%;
	left: 0;
	top: rem(120);
	height: calc(100% - 120px);
	z-index: 10;
	background-color: rgba(#404040, 0.8);
	backdrop-filter: blur(#{rem(50)});
	justify-content: flex-end;
	opacity: 0;
	visibility: hidden;
	transform: translateY(5%);
	transition: transform 0.2s cubic-bezier(0.0, 0.0, 0.2, 1),  opacity 0.2s ease, visibility 0.2s ease;

	&.show {
		transform: translateY(0);
		visibility: visible;
		opacity: 1;
	}

	@media(max-width: 991px) {
		top: rem(80);
		height: calc(100% - 80px);
	}

	@media(max-width: 744px) {
		top: rem(50);
		height: calc(100% - 50px);
	}

	&__top {
		padding: rem(17) 0;

		.lang-block {
			justify-content: flex-end;

			&__link {
				color: #737488;
			}
		}
	}

	&__body {
		width: 100%;
		height: 100%;
		max-width: rem(408);
		display: flex;
		flex-direction: column;
		background-color: #fff;
		overflow: auto;
		overflow-x: hidden;
		padding: 0 rem(16) rem(60);

		@media(min-width: 745px) {
			padding: 0 rem(40) rem(60);
		}
	}

	&__links {
		margin: rem(17) 0 0;
	}

	&__pc {
		margin: rem(36) 0 0;
		padding: 0 rem(20);

		.mpc-link + .mpc-link {
			margin: rem(11) 0 0;
		}
	}

	&__pc-title {
		font-family: $secFont;
		font-size: rem(14);
		color: $colorBlueDark;
		margin: 0 0 rem(14);
	}

	&__dop-links {
		display: flex;
		align-items: center;
		flex-direction: column;
		margin: rem(28) 0 0;

		a {
			display: flex;
			justify-content: center;
			position: relative;
			font-size: rem(16);
			font-weight: 400;
			color: $colorBlueDark;
			border-bottom: 1px solid #dbebfc;
			padding: rem(12) 0;
			min-width: rem(180);

			&:hover {
				color: $colorBlueLight;
			}
		}
	}

	.schedule {
		margin: rem(33) auto 0;

		&__body-wrap {
			transform: translateX(2px);
		}
	}

	&__soc-title {
		font-family: $secFont;
		font-size: rem(18);
		color: $colorBlueDark;
		text-align: center;
		margin: 0 0 rem(14);
	}

	&__soc {
		margin: rem(18) 0 0;
	}

	&__soc-list {
		display: flex;
		justify-content: center;
		gap: rem(12);
	}
}

.emp-link {
	display: flex;
	align-items: center;
	gap: rem(5);

	&__icon {
		width: rem(20);
		height: rem(20);

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		font-size: rem(14);
		font-weight: 400;
		text-transform: lowercase;
		line-height: 1;
		color: $colorBlueLight;
		position: relative;

		&::after {
			content: '';
			position: absolute;
			top: calc(100% + #{rem(1)});
			left: 0;
			width: 100%;
			height: rem(1);
			z-index: 2;
			transform: scaleX(0);
			transform-origin: left;
			transition: transform 0.2s ease;
			background-color: $colorBlueDark;
		}

		&::before {
			content: '';
			position: absolute;
			top: calc(100% + #{rem(1)});
			left: 0;
			width: 100%;
			height: rem(1);
			z-index: 1;
			transform: scaleX(1);
			transform-origin: left;
			transition: transform 0.2s ease;
			background-color: $colorBlueLight;
		}

	}

	&:hover {

		.emp-link__text {
			color: $colorBlueDark;

			&::after {
				transform: scaleX(1);
			}
		}

	}
}

.mpc-link {
	display: flex;
	align-items: center;
	gap: 10px;

	&__icon {
		width: rem(20);
		height: rem(20);

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		font-size: rem(14);
		line-height: 1;
		color: $colorBlueDark;
		position: relative;

		&::after {
			content: '';
			position: absolute;
			top: calc(100% + #{rem(-1)});
			left: 0;
			width: 100%;
			height: rem(1);
			z-index: 2;
			transform: scaleX(0);
			transform-origin: left;
			transition: transform 0.2s ease;
			background-color: $colorBlueLight;
		}

	}

	&:hover {
		color: $colorBlueLight;

		.mpc-link__text {
			&::after {
				transform: scaleX(1);
			}
		}

	}
}

.m-category {
	display: flex;
	justify-content: flex-end;
	border-radius: rem(16);
	overflow: hidden;
	background-color: #b5d8fd;
	width: 100%;
	height: rem(76);
	position: relative;

	&__img {
		width: rem(120);
		height: rem(76);
		margin: 0 rem(31) 0 0;
		background-image: url('data:image/webp;base64,');
		background-repeat: no-repeat;
		background-position: center;
		background-size: 100% 100%;
		position: relative;

		&::after {
			content: '';
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			max-width: rem(300);
			top: 50%;
			left: 50%;
			z-index: 1;
			transform: translate(-50%, -50%);
			border-radius: 100%;
			background-color: rgba(#fff, 0.6);
			filter: blur(60px);
		}

		img {
			width: 100%;
			height: 100%;
			position: relative;
			z-index: 2;
		}
	}

	&__title {
		position: absolute;
		top: 50%;
		left: rem(20);
		z-index: 1;
		transform: translateY(-50%);
		font-family: $secFont;
		font-size: rem(24);
		font-weight: 600;
		color: $colorBlueDark;

		@media(max-width: 360px) {
			font-size: 6.3vw;
		}
	}

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(24);
		height: rem(24);
		position: absolute;
		top: 50%;
		right: rem(11);
		z-index: 1;
		transform: translateY(-50%);
		transform-origin: top;
		transition: transform 0.2s ease;

		img {
			width: 100%;
			height: 100%;
		}
	}

	.active-m & {
		.m-category__icon {
			transform: scaleY(-1) translateY(-50%);
		}
	}

}

.banner-block {
	display: block;
	width: 100%;
	height: auto;
	min-height: rem(100);
	position: relative;

	//@media(min-width: 640px) {
	//	height: rem(200);
	//}
	//
	//@media(min-width: 1200px) {
	//	height: rem(160);
	//}

	&__img-link {
		display: block;
		width: 100%;
	}

	.mid-btn {
		width: 100%;
		max-width: rem(320);
		position: absolute;
		z-index: 1;

		@media(max-width: 430px) {
			position: relative;
			max-width: 100%;
			margin: rem(10) 0 0;
		}
	}

	&--right {
		.mid-btn {
			@media(min-width: 431px) {
				top: 52%;
				left: 70%;
				transform: translate(-50%, -50%);

				@media(max-width: 744px) {
					left: 50%;
				}
			}
		}
	}

	&__img {
		display: block;
		width: 100%;
		height: 100%;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.product-count {
	display: flex;
	align-items: center;
	width: rem(80);
	height: rem(28);
	border: 1px solid #dbebfc;
	border-radius: rem(9);
	padding: 0 rem(9);

	&__button {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(12);
		height: rem(12);
		flex-shrink: 0;
		color: $colorBlueDark;
		transition: color 0.2s ease;

		svg {
			width: 100%;
			height: 100%;
			transition: fill 0.2s ease;
		}

		&:hover, &:focus {

			svg {
				fill: $colorBlueLight;
			}
		}
	}

	&__input {
		width: 100%;
		height: 100%;
		text-align: center;
		font-size: rem(16);
		color: $colorBlueDark;
	}
}

.reg-mob {
	margin: rem(32) 0 0;

	&__btns {
		display: flex;
		justify-content: space-between;
		margin: rem(8) 0 0;

		.mid-btn {
			font-size: rem(14);
			min-width: initial;
			width: 48%;
			height: rem(42);
		}
	}
}

.reg-board {
	width: 100%;
	border-radius: rem(12);
	background-color: #dbebfc;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	padding: rem(24) rem(14);

	&__title {
		font-size: rem(16);
		font-weight: bold;
		color: #202454;
		margin: 0 0 rem(8);
	}

	&__sub {
		font-size: rem(14);
		color: #202454;
		margin: 0 0 rem(7);
	}

	&__sale {
		display: flex;
		align-items: center;
		gap: 5px;

		span {
			line-height: 1;
			font-size: rem(24);
			font-weight: 900;
			color: #202454;
		}
	}
}

